.card {
    max-width: 1000px;
    /*min-width: 300px;*/
    min-height: 200px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    padding-left: 20px;
    padding-top: 30px;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-left: -1.2rem;
    float: left;
    transition: 0.5s;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.card h2 {
    color: #ededed;
    font-size: 17px;
}

.card p {
    color: #b0b0b0;
}

.card img {
    height: 50px;
    width: 50px;
}

.card svg {
    height: 50px;
    width: 50px;
}

.card:hover {
    transition: 0.5s;
    -webkit-transform: translateY(-2rem);
            transform: translateY(-2rem);
    z-index: 3 !important;
}

.fa-lock {
    color: #f55;
}

